        </div>
        <label v-if="!isLoading" for="img-upload" 
        class="img-upload-label"
        @drop.prevent="handleFile"
        @dragover.prevent="isDragOver = true"
        @dragleave="isDragOver = false"
        :class="{ drag: isDragOver }">
          <svg
