        <div class="img-container">
          <img :src="this.newImgUrl" alt="" />
        </div>
        <div class="new-story-caption-container" id="new-story-caption-container">
          <textarea
            v-model="newStoryText"
            name=""
            class="caption-text"
            cols="30"
            rows="10"
            placeholder="Add a caption..."
          >
          </textarea>
          <!-- <span>
                <svg aria-label="Emoji" class="_8-yf5 " fill="#262626" height="24" role="img" viewBox="0 0 48 48" width="24"><path d="M24 48C10.8 48 0 37.2 0 24S10.8 0 24 0s24 10.8 24 24-10.8 24-24 24zm0-45C12.4 3 3 12.4 3 24s9.4 21 21 21 21-9.4 21-21S35.6 3 24 3z"></path><path d="M34.9 24c0-1.4-1.1-2.5-2.5-2.5s-2.5 1.1-2.5 2.5 1.1 2.5 2.5 2.5 2.5-1.1 2.5-2.5zm-21.8 0c0-1.4 1.1-2.5 2.5-2.5s2.5 1.1 2.5 2.5-1.1 2.5-2.5 2.5-2.5-1.1-2.5-2.5zM24 37.3c-5.2 0-8-3.5-8.2-3.7-.5-.6-.4-1.6.2-2.1.6-.5 1.6-.4 2.1.2.1.1 2.1 2.5 5.8 2.5 3.7 0 5.8-2.5 5.8-2.5.5-.6 1.5-.7 2.1-.2.6.5.7 1.5.2 2.1 0 .2-2.8 3.7-8 3.7z"></path></svg>
              </span> -->
          <button @click="addStory()">Publish</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { uploadImg } from "@/services/img-upload-service.js";

export default {
  // components:{
  //   uploadImg,
  // },
  // props: {

  // },
  //===========================
  data() {
    return {
      loggedInUser: {},
      isLoading: false,
      // background: true,
      screen1: true,
      screen2: false,
      newStoryText: '',
      newImgUrl: '',
      isDragOver: false,
    };
  },
  //===========================
  methods: {
    // async onUploadImg(ev) {
    //   this.isLoading = true;
    //   const res = await uploadImg(ev);
    //   this.isLoading = false;
    //   // console.log(res)
    //   this.newImgUrl = res.url;
    //   this.switchToScreen("2");
    // },

    switchToScreen(screen) {
      if (screen === "1") {
        this.screen1 = true;
        this.screen2 = false;
        this.newImgUrl = "";
      } else {
        this.screen1 = false;
        this.screen2 = true;
        this.newStoryText = "";
      }
    },
    closeMenu() {
      this.$emit("close");
    },
    async addStory() {
      const payload = {
        text: this.newStoryText,
        imgUrl: this.newImgUrl,
        createdAt: Date.now(),
        owner: {
          _id: this.loggedInUser._id,
          username: this.loggedInUser.username,
          profileImgUrl: this.loggedInUser.profileImgUrl,
        },
        likedBy: [],
        comments: [],
      };
      await this.$store.dispatch("addStory", payload);
      this.$emit("saved");
    },

    handleFile(ev) {
      let file;
      if (ev.type === "change") file = ev.target.files[0];
      else if (ev.type === "drop") file = ev.dataTransfer.files[0];
      this.onUploadImg(file);
    },

    async onUploadImg(file) {
      this.isLoading = true;
      this.isDragOver = false;
      const res = await uploadImg(file);
      // this.$emit("save", res.url);
      // console.log('Res: ', res)
      this.newImgUrl = res.url;
      this.isLoading = false;
      this.switchToScreen("2");
    },
    
    async getLoggedInUser(){
        this.loggedInUser = await this.$store.dispatch('getLoggedInUser')
    },
     // async onUploadImg(ev) {
    //   this.isLoading = true;
    //   const res = await uploadImg(ev);
    //   this.isLoading = false;
    //   // console.log(res)
    //   this.newImgUrl = res.url;
    //   this.switchToScreen("2");
    // },

  },
    created() {
      this.getLoggedInUser()
      // this.background = true;
      this.screen1 = true;
    },
};
</script>



